.profile_item {
  background-color: #ffffff;
  margin: 10px 0;
}

.profile_inner {
  display: flex;
}

.profile__status {
  width: 100%;
  background-color: white;
  margin: 10px 0;
  padding: 5px 10px;
  min-height: 35px;
}

.profile__status input {
  width: 100%;
}

.profile_info {
  display: flex;
  margin-left: 30px;
  width: 100%;
}

.profile_title {
  font-size: 20px;
  font-weight: 500;
  margin: 10px 0;
}

.profile_info_inner {
  width: 100%;
}

.profile_info__item {
  position: relative;
  margin-bottom: 5px;
}

.profile_info__item span {
  position: absolute;
  right: 5%;
}

.profile_bg_img {
  object-fit: cover;
  height: 200px;
  width: 100%;
}

.avatar_img_container {
  position: relative;
  /* cursor: pointer; */
}

.avatar_img {
  width: 200px;
  height: 100%;
  object-fit: cover;
}

.avatar_change {
  text-align: center;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.avatar_img_container:hover .custom-file-upload{
  opacity: 1;
}

.avatar_change input[type="file"] {
  display: none;
}
.custom-file-upload {
  width: 100%;
  height: 100%;
  background-color: rgba(74, 118, 168, .3);
  /* border: 1px solid #ccc; */
  display: flex;
  
  cursor: pointer;

  opacity: 0;
  transition: all .2s;
}

.fa-cloud-upload {
  padding: 6px 12px;
  margin: auto;
  background-color: #fff;
}